import goodsHeader from './base/goodsHeader.vue'
import interfaces from '../../../utils/interface.js'
export default {
	data() {
		return {
			swiperList: [], //轮播图
			goodsInit: [], //初始化信息
			goodsData: [], 
			title_price: '',
			currentSwiper: 0, //轮播图下标
			detailsImg: [], //详情图
			goodsId : '', //商品id
			number: 1,  //出价次数
			time: '',  //倒计时时间
			price : '',
			priceArr: [], //测试价格
			price_count: '0', //出价人数
			jifen_log_length: '0', //出价记录
			buyKey : false , //是否可以拍得,  true关闭机器人
			shouBuy : false , //是否显示购买
			isPai : false , //是否开始拍卖
			animate : false,
			i: 2 , //随机加价秒数

		}
	},
	components: {
		goodsHeader
	},

	methods: {
		async initData_z(e) {

			this.request({
				url: interfaces.jifen_goods, //积分拍卖商品页
				method: "POST",
				data: {
					jgoods_id: e
				},
				success: ((res) => {
					console.log(res)

					this.goodsInit = res.data[0]; //初始化信息
					console.log(this.goodsInit)
					this.swiperList = res.thumbimg //提取轮播图

				})
			});
			this.request({
				url: interfaces.jifen_log, //出价记录
				method: "POST",
				data: {
					jgoods_id: e
				},
				success: ((res) => {
					if (res.error_code == 0) {

					}
				})
			});
			this.request({
				url: interfaces.jifen_content, //评价
				method: "POST",
				data: {
					jgoods_id: e
				},
				success: ((res) => {
					console.log(res)
				})
			})
			this.request({
				url: interfaces.jifen_order, //往期成交
				method: "POST",
				data: {
					jgoods_id: e
				},
				success: ((res) => {
					console.log(res)
				})
			})
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		showBigImg(src, srclist, isSwiper) { //预览大图
			let srcInit = 'https://www.tiktokshopee.com' + src;
			let imgList = [];
			if (isSwiper) { //查看轮播图
				srclist.forEach((item) => {
					imgList.push('https://www.tiktokshopee.com' + item) //http微信小程序真机不显示 ，改成https即可
				})
			} else { //查看详情图
				srclist.forEach((item) => {
					imgList.push('https://www.tiktokshopee.com' + item.img_url)
				})
			}
			console.log(imgList)
			uni.previewImage({
				current: srcInit,
				urls: imgList,
			})
		},
		navTo(url) { //页面跳转
			uni.navigateTo({
				url
			})
		},
		toOrder(item) { //去下单
			console.log(item)
			item.game = '积分';
			item.num = 1;
			item.kan_id = item.id;
			uni.navigateTo({
				// url: '../../order/base/createOrder?goodsInfo=' + JSON.stringify(item)
				url: '../../../pages/order/base/createOrder?goodsInfo=' + JSON.stringify(item)
			})
		},
		minus() {
			this.number--;
			if (this.number < 1) {
				this.number = 1;
			}
		},
		add() {	
			this.number++;
		},
		get_time() {
			let self = this;
			let timer = setInterval(() => {
				self.time--;
				if (self.time < self.i) {
					if(!self.buyKey){//如果没有达到可拍卖的价格
					    this.animate = true;
						self.init()
					}
					if(self.time < 1){
						this.shouBuy = true;
						this.animate = false;
						console.log('恭喜你拍得此商品')
						clearInterval(timer)
					}
				}
			}, 1000)
		},
		init(e , p ) {
			this.i = parseInt(10 * (Math.random() / 2) + 1)
			console.log(this.i)
			if(parseInt(this.price) > 142 || e){ //
				console.log('到达阈值')
			}else{
				this.time = 9;
				if(p){
					this.price = parseFloat(this.price) + p;
				}else{
					this.price = parseFloat(this.price) + 0.1;
				}
				this.showMarquee()
			}	
		},
		gameStart(){//点击游戏开始
			this.isPai = true;
			this.get_time()
		},
		showMarquee(){//价格变动
			this.price = this.price.toFixed(2);
			setTimeout(() => {
				this.priceArr.push(this.price)
				this.priceArr.shift();
				this.animate = false;
			},500)
		},
		addPrice() { //用户点击加价
		   if(this.isPai){ //如果拍卖已经开始
				this.request({
					url: interfaces.chujia, //拍卖出价
					method: "POST",
					data: {
						jgoods_id: this.goodsId,
						num : this.number
					},
					success: ((res) => {
						console.log(res)
						if(res.error_code == 0){//有积分
							this.animate = true;
							let add_price = this.number/10;
							this.buyKey = false; //每次加价重置
							if(parseInt(this.price) >= 130){ //如果到达可购买的价格,如再没有拍卖着，则倒计时可获得
								this.time = 9;
								this.price = parseFloat(this.price) + add_price;
								this.showMarquee();
							    this.buyKey = true;
							}else{
								this.init(this.buyKey ,add_price)
							}	
						}
						else if(res.error_code == 1){
							console.log('555')
							uni.showToast({
								icon: "none",
								title: res.data
							})
							setTimeout(function(){
								uni.navigateTo({
									url: '../../../pages/public/login'
								})
							},700)
							return;
						}else if(res.error_code == 4){
							console.log('555')
							uni.showToast({
								icon: "none",
								title: res.data
							})
							setTimeout(function(){
								uni.navigateTo({
									url: '../swop/base/toBuy'
								})
							},700)
							return;
						}
					})
				})
			}
		},
		Arr_price(e){
			for(let i = 0 ; i < 1 ; i++){
				this.priceArr[i] = e;
			}
		}
	},
	
	created() {
		// setInterval(this.showMarquee, 2000)
	},
	onLoad(option) {
		let data = JSON.parse(option.data);
		console.log(data)
		this.time = data.time;
		this.price = data.price;
		this.Arr_price(this.price)
		this.initData_z(data.jgoods);
		this.goodsId = data.jgoods;
		// this.get_time()
	}
}
